.header .container, .main-block .container {
    padding: 0 15px;
}

@media screen and (min-width: 576px) {
    .header .container, .main-block .container {
        padding: 0 35px;
    }
}

@media screen and (min-width: 768px) {
    .header .container, .main-block .container {
        padding: 0 50px;
    }
}

@media screen and (min-width: 1280px) {
    .header .container, .main-block .container {
        padding: 0;
        width: 1140px;
        margin: 0 auto;
    }
}

.header {
    background-color: #fff;
}

@media screen and (min-width: 768px) {
    .header {
        box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    }
}

.header__inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;
    height: 60px;
}

@media screen and (min-width: 768px) {
    .header__inner {
        height: 100px;
    }
}

.header__wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
}

.header__logo {
    margin-right: 45px;
}

@media screen and (min-width: 576px) {
    .header__logo {
        margin-right: 36px;
    }
}

@media screen and (min-width: 768px) {
    .header__logo {
        height: 23px;
    }
}

.header__phone-city {
    font-size: 14px;
    font-weight: 400;
    line-height: 17.07px;
    color: #5286EC;
    text-decoration: none;
}

@media screen and (min-width: 576px) {
    .header__phone-city {
        display: none;
    }
}

.header__phone-number {
    text-decoration: none;
    color: #37373f;
}

@media screen and (min-width: 80px) {
    .header__phone-number {
        display: none;
    }
}

@media screen and (min-width: 576px) {
    .header__phone-number {
        display: initial;
    }
}

.header span {
    color: #b3b3b3;
}

.header__user {
    width: 20px;
    height: 20px;
}

@media screen and (min-width: 1280px) {
    .header__user {
        display: none;
    }
}

.main-block {
    background-color: silver;
    color: #fff;
    background-image: url(./../images/main-block/helicopter.jpg);
    background-size: cover;
    background-position-x: 62%;
}

@media screen and (min-width: 768px) {
    .main-block {
        background-position: center;
    }
}

.main-block__inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    height: 655px;
}

@media screen and (min-width: 768px) {
    .main-block__inner {
        height: 840px;
    }
}

.main-block__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 39px;
    color: #fff;
    max-width: 247px;
    margin-top: -6px;
}

@media screen and (min-width: 768px) {
    .main-block__title {
        max-width: 596px;
        font-size: 40px;
        line-height: 49px;
    }
}

@media screen and (min-width: 1280px) {
    .main-block__title {
        max-width: 887px;
        width: 100%;
    }
}

.main-block__title span {
    color: #FC9B09;
}

.main-block__price-box {
    height: 93px;
    width: 208px;
    background-image: url(./../images/main-block/price-box.svg);
    background-repeat: no-repeat;
    margin: 36px 0 14px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    padding: 11px 15px;
    margin-left: -5px;
}

.main-block__wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}

.main-block__price-label {
    font-size: 18px;
    font-weight: 400;
    line-height: inherit;
    color: inherit;
    margin-bottom: 4px;
}

.main-block__price {
    font-size: 35px;
    font-weight: 800;
    line-height: inherit;
    color: inherit;
}

.main-block__percent {
    border-radius: 50%;
    -webkit-filter: drop-shadow(0px 4px 15px rgba(255, 255, 255, 0.25)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    filter: drop-shadow(0px 4px 15px rgba(255, 255, 255, 0.25)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    max-height: 47px;
    margin-top: 5px;
    -webkit-transform: translateX(4px);
    transform: translateX(4px);
}

.main-block__btn {
    width: 275px;
    height: 68px;
    border: 3px solid #fff;
    border-radius: 7px;
    background-color: transparent;
    cursor: pointer;
    font-size: 16px;
    font-weight: 900;
    line-height: 20px;
    color: white;
    -webkit-filter: drop-shadow(0px 4px 70px rgba(255, 255, 255, 0.7)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    filter: drop-shadow(0px 4px 70px rgba(255, 255, 255, 0.7)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
}
